/* Stylesheet for laroux.ui.js
----------------------------------------------------------*/
.larouxFloatDiv
{
    font-size: 14px;

    position: fixed;
    z-index: 100002;
    top: 10px;
    left: 0;

    width: 100%;
}
.larouxMsgBox
{
    font: 14px sans-serif;

    width: 300px;
    margin: auto;
    padding: 6px 10px 6px 10px;

    opacity: 0;
    color: #393939;
    border: 1px solid #808080;
    border-radius: 4px;
    background-color: #f0f0f0;
    box-shadow: 3px 3px 5px #000;
}
.larouxMsgBox h3
{
    font-size: 14px;
    font-weight: bold;

    margin: 0 0 4px 0;
}
.larouxFade
{
    -webkit-transition: opacity .25s ease-in-out;
       -moz-transition: opacity .25s ease-in-out;
            transition: opacity .25s ease-in-out;
}
